html{
    height: 100%;
    width: 100%;
}

body{
    font-family: "raleway", Arial, sans-serif;
    position: relative;
    font-size: 13px;
    line-height: 1.42;
    font-weight: 600;
    -webkit-font-smoothing: antialiased;
    grid-gap: 160px;
    
    width: 100%;
    min-height: 100%;
    display: grid;
    grid-template-rows: 1fr auto;
}

a{
    text-decoration: none;
}

img{
    width: 100%;
    height: auto;
}

.title_all{
    font-size: 42px;
    font-weight: 900;
    line-height: 65px;
    margin-bottom: 10px;
    margin-top: 150px;
}

h1, h2, h3, h4, h5, h6{
    margin-top: 0;
    margin-bottom: 0;
    font-weight: normal;
}

hr{
    opacity: .1;
}

*{
    box-sizing: border-box;
}
//-----------------------------footer-----------------------------
.footer{
    z-index: 10;
}

.footer_socials{
    height: 100px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
}

.footer_socials-wrap{
    justify-self: center;
    align-self: center;
    display: grid;
    grid-gap: 20px;
}

.socials{
    &:hover .socials_icon{
        fill-opacity: 1;
        transition: all ease-in .3s;
    }
}

.socials_icon{
    width: 40px;
    height: auto;
    fill-opacity: .3;
}

//-----------------------------site_name-----------------------------
.site_name{
    position: absolute;
    top: 20px;
    left: 8%;
    z-index: 100;
}

.site_link{
    font-size: 20px;
    font-weight: 700;
}

.qr-code{
    display: none;
}

//-----------------------------fixed_menu-----------------------------
.fixed_menu{
    width: 60px;
    height: 60px;
    top: 2%;
    right: 2%;
    width: 60px;
    height: 60px;
    position: fixed;
    z-index: 200;
}

.fixed_menu-link{
    width: 100%;
    height: 100%;
    position: absolute;
    display: grid;
}

.fixed_menu-block{
    justify-self: center;
    align-self: center;
}

.fixed_menu-dash-wrap{
    height: 10px;
    display: grid;
}

.fixed_menu-dash{
    width: 20px;
    height: 5px;
    justify-self: center;
    align-self: center;
}

.dash__transition{
    transition: 0.2s;
}

.active_menu{
    right: calc(2% + 5px);

    .dash_one{
        transform: rotate(45deg) translateY(5px) translateX(5px);
        transition: 0.2s;
    }

    .dash_two{
        transform: rotate(-45deg) translateY(-3px) translateX(1px);
        transition: 0.2s;
    }

    .dash_three{
        opacity: 0;
        transition: 0.1s;
    }
}

// -----------------------------Navi_menu-----------------------------
.navi_menu{
    top: -100%;
    width: 100%;
    height: 100%;
    z-index: 110;
    display: grid;
    position: fixed;
    transition: .1s;
}

.menu-block{
    justify-self: center;
    align-self: center;
    font-weight: 600;
    font-size: 35px;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;

    & .socials {
        margin: 0 auto;
    }

    & .socials_icon {
        fill-opacity: 1;
    }
}

.navi_menu-list{
    justify-self: center;
    align-self: center;

    &:hover{
        transition: all ease-in .2s;
    }
}

.navi__menu--active{
    top: 0;
}

//-----------------------------fixed_print-----------------------------
.fixed_print{
    bottom: 2%;
    right: 2%;
    width: 60px;
    height: 60px;
    position: fixed;
    z-index: 40;
}

.fixed_print-fa{
    width: 100%;
    height: 100%;
    display: grid;
}

.print-fa-svg{
    width: 26px;
    justify-self: center;
    align-self: center;
}

//-----------------------------fixed_phone-----------------------------
.fixed_phone{
    bottom: 2%;
    right: 2%;
    width: 60px;
    height: 60px;
    position: fixed;
    z-index: 40;
}

.fixed_phone-fa{
    width: 100%;
    height: 100%;
    display: grid;
}

.phone-fa-svg{
    width: 26px;
    justify-self: center;
    align-self: center;
}

//-----------------------------fixed-socials-----------------------------
.fixed-socials {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
}
::-webkit-scrollbar-button {
    background-image:url('');
    background-repeat:no-repeat;
    width:0px;
    height:0px;
}
    
::-webkit-scrollbar-track {
    background-color:#8e8e8e;
}
    
::-webkit-scrollbar-thumb {
    border-radius: 0px;
    background-color:#fff;

    &:hover{
        background-color:#fff;
    }
}
    
::-webkit-resizer{
    background-image:url('');
    background-repeat:no-repeat;
    width:0px;
    height:0px;
}
    
::-webkit-scrollbar{
    width: 5px;
}
//-----------------------------wrapper-----------------------------

.wrapper{
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-template-rows: 1fr 1fr 1fr;
    display: grid;
    z-index: 10;
}


.container{
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 3;
    justify-self: center;
    display: grid;
    grid-gap: 35px;

    width: 75%;
}

.decor-gray{
    grid-row-start: 1;
    grid-row-end: 3;
    position: relative;
    overflow: hidden;
}

//-----------------------------header-----------------------------
.header{
    display: grid;
}

.header_block{
    width: 70%;
}

//-----------------------------content-----------------------------

.content{
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-gap: 20px;
    grid-auto-flow: column;
}

.menu_left, .menu_right{
    display: grid;
}

.menu_block{
    overflow: hidden;

    &:hover .menu_block-wrapper{
        opacity: 1;
        transition: all ease-in .2s;
    }
}

.menu_block-wrapper{
    display: grid;
    width: 100%;
    height: 100%;
    opacity: 0.8;
}

.menu_block-content{
    align-self: end;
}

.menu_block-title{
    margin-left: 15%;
    margin-bottom: 3%;
    font-weight: 900;
    font-size: 18px;
}

.menu_block-text{
    margin-left: 15%;
    margin-bottom: 35px;
    font-weight: 700;
}

.block_for-me{
    grid-row-start: 1;
    grid-row-end: 5;
    height: 200px;
}

.block_photo{
    grid-row-start: 5;
    grid-row-end: 9;
}

.block_contacts{
    grid-row-start: 9;
    grid-row-end: 14;
}

.block_projects{
    grid-row-start: 1;
    grid-row-end: 6;
}

.block_career{
    grid-row-start: 6;
    grid-row-end: 11;
}
//-----------------------------about-----------------------------

.wrapper_about {
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
    grid-template-rows: 1fr 1fr 1fr;
    display: grid;
}

.container_about {
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 3;
    justify-self: center;
    display: grid;
    grid-gap: 35px;

    width: 75%;
}

.maincontent {
    grid-column-start: 3;
    grid-column-end: 8;
    grid-row-start: 1;
    grid-row-end: 4;
    position: relative;
    display: grid;
    overflow: hidden;
}

.about {
    width: 90%;
    margin-top: 10%;
    margin-left: 1%;
    margin-bottom: 1%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-gap: 10px;
    z-index: 5;
}

.about_block-left {
    display: grid;
}

.about_block-right {
    width: 95%;
}


.about_my-photo {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
}

.about_right-title {
    font-size: 14px;
    grid-column-start: 2;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 3;
}

.about_position {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 4;
}

.about_position-text {
    grid-column-start: 2;
    grid-column-end: 5;
    grid-row-start: 3;
    grid-row-end: 4;
}

.about_skills {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 4;
    grid-row-end: 5;
}

.about_skills-text {
    grid-column-start: 2;
    grid-column-end: 5;
    grid-row-start: 4;
    grid-row-end: 5;
}

.about_my-photo {
    display: grid;
}

.about_photo-wrap {
    width: 40%;
    min-width: 100px;
    overflow: hidden;
    justify-self: center;
    align-self: center;
    filter: saturate(0.5);
    display: grid;
    transition: all ease-in .2s;
}

.about_photo-pic {
    justify-self: center;
    align-self: center;
}

.about_title {
    justify-self: end;
    font-size: 14px;
    font-weight: 800;
}

.about_block-right {
    &:hover {
        transition: all ease-in .7s;
    }
}

.about_position-text,
.about_skills-text {
    font-size: 15px;
    font-weight: 400;
}
//-----------------------------project-----------------------------

.project-header {
    position: absolute;
    padding-left: 8%;
    padding-bottom: 60px;
    width: 28%;
    z-index: 10;
}

.progect_gray {
    position: relative;
    display: grid;
    overflow: hidden;
}

.wrapper_project {
    margin: 0 auto;
    width: 65%;
    display: grid;
    z-index: 5;
}

.project__slide--wrap {
    display: grid;
}

.project_block {
    margin-top: 185px;
    width: 90%;
    justify-self: center;
    display: none;
    grid-gap: 50px;
}

.project__active {
    display: grid;
}

.project__fade {
    animation-name: project_fade;
    animation-duration: 1s;
}

@keyframes project_fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.project_top {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.project_top-right {
    grid-column-start: 2;
    grid-column-end: 4;
    transition: .5s;
}

.hr_for-tablets {
    display: none;
}

.project_center {
    display: grid;

    &:hover .progect_site-pic {
        opacity: 1;
        filter: grayscale(0);
        transition: all ease-in 0.2s;
    }
}

.progect_site-pic {
    opacity: 0.7;
    align-self: center;
    filter: grayscale(1);
}

.project_bottom-next, .project_bottom-prev {
    height: 40px;
    bottom: 10%;
    position: fixed;
    z-index: 100;
}

.project_counter {
    width: 70px;
    height: 40px;
    left: 50%;
    bottom: 12%;
    transform: translateX(-50%);
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .3s;
    opacity: .8;
    z-index: 100;

    &-active {
        font-size: 20px;
        font-weight: 800;
    }
    &-all {
        font-size: 16px;
        font-weight: 500;
    }
}

.project_bottom-prev {
    left: 2%;
}

.project_bottom-next {
    right: 2%;
}

.button_prev {
    justify-self: start;
}

.button_next {
    justify-self: end;
}

.button_custom {
    text-align: center;
    font-size: 16px;
    font-weight: 800;
    padding: 10px;
    transition: .3s;
    opacity: .8;

    &:hover {
        opacity: 1;
    }
}
//-----------------------------career-----------------------------

.content_career {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-gap: 20px;
    grid-auto-flow: column;
}

.block_fix-price {
    grid-row-start: 1;
    grid-row-end: 5;
    height: 200px;
}

.block_f-m-c {
    grid-row-start: 5;
    grid-row-end: 9;
}

.block_inditex {
    grid-row-start: 1;
    grid-row-end: 6;
}

.block_adidas {
    grid-row-start: 1;
    grid-row-end: 7;
}

.menu_block-brend {
    margin-left: 15%;
    margin-bottom: 35px;
    font-weight: 900;
    font-size: 18px;
}

.inditex_svg {
    width: 50%;
}

.adidas_svg {
    width: 25%;
}

//-----------------------------career-modal-----------------------------
.career-modal {
    position: fixed;
    display: none;
    width: 100vw;
    height: 100vh;
    z-index: 300;

    &__active {
        display: grid;
    }

    &__fade {
        animation-name: career-modal__fade;
        animation-duration: .3s;
    }

    @keyframes career-modal__fade {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }

    &__block {
        display: grid;
        position: relative;
        width: 70%;
        height: 500px;
        justify-self: center;
        align-self: center;
        overflow: hidden;
    }

    &__close {
        position: absolute;
        width: 40px;
        height: 40px;
        top: 0;
        right: 0;
        cursor: pointer;

        &::before {
            content: '';
            position: absolute;
            width: 20px;
            height: 5px;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) translateY(-50%) rotate(45deg);
        }

        &::after {
            content: '';
            position: absolute;
            width: 20px;
            height: 5px;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) translateY(-50%) rotate(-45deg);
        }
    }

    &__wrap {
        width: 90%;
        height: 450px;
        overflow: hidden;
        align-self: center;
        justify-self: center;
    }

    &__title {
        height: 100px;
        border-bottom: solid;
        overflow: hidden;
    }

    &__brend-fix {
        font-weight: 900;
        font-size: 18px;
    }

    &__brend-f-m-c {
        font-weight: 900;
        font-size: 18px;
    }

    &__inditex-svg {
        width: 110px;
    }

    &__adidas-svg {
        width: 50px;
    }

    &__content {
        height: 350px;
        overflow: auto;
        transition: 2s;
    }

}
//-----------------------------photo-----------------------------

.container_photo {
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 3;
    justify-self: center;
    display: grid;
    grid-gap: 35px;

    width: 75%;
}

.content_photo {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
}

.photo-media {
    transition: all ease-in .2s;
    display: grid;

    &:hover {
        transform: scale(1.3);
        z-index: 1;
    }

    &:hover .photo_pic {
        opacity: 1;
        transition: all ease-in .2s;
        filter: grayscale(0);
    }

    &:hover .gelen {
        filter: hue-rotate(20deg);
    }
}

.photo_pic {
    opacity: 0.5;
    align-items: center;
    filter: grayscale(1);
    transition: all 0.3s ease-in;
}

.gelen {
    filter: grayscale(1);
}
//-----------------------------contact-----------------------------
.wrapper_contact{
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
    grid-template-rows: 1fr 1fr 1fr;
    display: grid;
}

.container_contact{
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 3;
    justify-self: center;
    display: grid;
    grid-gap: 35px;

    width: 75%;
}

.maincontent_contact{
    grid-column-start: 3;
    grid-column-end: 8;
    grid-row-start: 1;
    grid-row-end: 4;
    display: grid;
    position: relative;
    overflow: hidden;
}

.contact_about{
    margin-top: 20%;
    margin-left: 1%;
    margin-bottom: 1%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    align-self: end;
}

.contact_block{
    display: grid;
    min-height: 300px;
    z-index: 5;
}

.contact_content{
    width: 80%;
    justify-self: center;
    align-self: center;
    line-height: 32px;
    font-size: 16px;
}

.contact {
    &:hover{
        transition: all ease-in 0.2s;
    }
}
